.waitlist__main--show, .waitlist__email--show, .waitlist__book--show, .waitlist__betatest--show {
    display: block;
}

.waitlist__main--hide, .waitlist__email--hide, .waitlist__book--hide, .waitlist__betatest--hide {
    display: none;
}

.waitlistMain {
    width: 90vw;
    max-width: 1200px;
    height: 800px;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 50px;
    position: relative;
    padding: 20px;
}

.waitlistMain__header {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
}

.waitlistMain__title {
    font-size: 60px;
}

.waitlistMain__subtitle {
    font-size: 15px;
    line-height: 20px;
    text-align: center;
}

.waitlistMain__plans {
    width: 100%;
    max-width: 1000px;
    display: flex;
    justify-content: space-around;
}

.waitlistMain__plan {
    width: 300px;
    height: 450px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    border-radius: 20px;
    padding: 30px;
    border: 2px solid #2F2F2F;
}

.waitlistMain__plan--betatest {
    border: 2px solid #6391C7;
}

.waitlistMain__planName {
    font-size: 20px;
}

.waitlistMain__planPrice {
    font-size: 50px;
}

.waitlistMain__properties {
    height: 200px;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.waitlistMain__property {
    display: flex;
    align-items: center;
    gap: 10px;
}

.waitlistMain__planButton {
    height: 45px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
}

.waitlistMain__planButton:hover {
    cursor: pointer;
}

.waitlistMain__planButton > p {
    font-size: 15px;
}

.waitlistMain__exit {
    position: absolute;
    top: 30px;
    right: 30px;
}

.waitlistMain__exit:hover {
    cursor: pointer;
}

.waitlistEmail {
    width: 400px;
    height: 500px;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 50px;
    position: relative;
}

.waitlistEmail__header {
    position: absolute;
    top: 0;
    height: 50px;
    width: 100%;
    border-radius: 20px 20px 0 0;
}

.waitlistEmail__exit {
    width: 25px;
    height: 25px;
    position: absolute;
    top: 12px;
    right: 12px;
}

.waitlistEmail__exit:hover {
    cursor: pointer;
}

.waitlistEmail__starting {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    position: relative;
    top: 35px;
}

.waitlistEmail__title {
    font-size: 40px;
}

.waitlistEmail__subtitle {
    font-size: 12px;
    line-height: 25px;
    text-align: center;
    width: 270px;
}

.waitlistEmail__inputName {
    font-size: 15px;
}

.waitlistEmail__inputs {
    width: 320px;
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.waitlistEmail__input {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.waitlistEmail__inputHolder {
    border-radius: 5px;
    padding: 10px 15px;
    width: 100%;
}

.waitlistEmail__inputHolder > input {
    border: 0;
    outline: 0;
    background-color: transparent;
    width: 100%;
}

.waitlistEmail__submitButton {
    width: 320px;
    height: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
